'use client';
import { useState } from 'react';
<FontAwesomeIcon icon="fa-regular fa-bars" />

export default function SideBar() {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <div className="flex h-screen">
      {/* Toggle Button */}
      <button 
        onClick={() => setIsOpen(!isOpen)}
        className="p-2 bg-gray-800 text-white rounded-md fixed top-4 left-4 z-50"
      >
        <icon size={24} />
      </button>
      
      {/* Sidebar */}
      <div className={`bg-gray-800 text-white w-64 p-4 transition-all duration-300 fixed h-full ${isOpen ? 'translate-x-0' : '-translate-x-full'}`}>
        <h2 className="text-xl font-bold">Navigation</h2>
        <ul className="mt-4 space-y-2">
          <li><a href="#" className="block p-2 hover:bg-gray-700 rounded">Home</a></li>
          <li><a href="#" className="block p-2 hover:bg-gray-700 rounded">About</a></li>
          <li><a href="#" className="block p-2 hover:bg-gray-700 rounded">Contact</a></li>
        </ul>
      </div>
      
    </div>
  );
}
